@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: rgb(18, 31, 61);
  color: #fff;
}

section {

    background-color: rgb(25, 43, 85);
    
    max-width: 70%;
    max-height: 80%;
    padding: 1rem;
    margin-bottom: 1rem;
    border-radius: 1rem;
    align-content: center;

    color: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
    
}

img{
  border-radius: 1rem;
  max-width: 90%;
  max-height: 30%;
}

p{
  text-align: justify;
}



h1,h2 {
  font-family: 'Concert One', cursive;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}


.Navbar {
  width: 100%;
  padding: 0.4rem 0;
  background-color: #2060CA;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Navbar__brand {
  color: #ffffff;
  display: flex;
  align-items: center;
}

.Navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}

.Navbar__brand-logo {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}
.Footer {
    width: 100%;
    padding: 0.2rem 0;
    margin-top: 1rem;
    margin-bottom: 0rem;
    background-color: #204d91;
    text-align: center;
    align-content: center;
    align-items: center;
  }

.Home {

    color: #ffffff;

    display: flex;
    align-items: center;
    
    
}

.Home_hero{
  
  width: 100%;
  padding: 5rem;

  background: url(/static/media/stars.d4d2376b.svg), #1B1B25;
  background-repeat: repeat;
  color: #ffffff;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.Home__col {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}


@media screen and (min-width: 300px) {
  

  
}

@media screen and (min-width: 768px){
  .Home_hero{
  
    padding: 7rem;
  
  }
  
}

.NotFound {
    width: 100%;
    min-height: calc(100vh - 60px);
    color: #ffffff;
    display: flex;
    
  }
  .NotFound_hero {
    width: 100%;
    padding: 5rem 0;
    background: url(/static/media/stars.d4d2376b.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 3rem;
    color: #ffffff;
    text-align: center;
    
  }
